<template>
    <div v-if="showBlock" @click="stopTimer" 
    class="flex justify-center items-center w-96 h-80 max-[428px]:w-[90%] max-[428px]:h-[300px] px-5 max-sm:px-2 mx-auto my-10 
     bg-green-500 rounded text-center  ">
        <p class="text-white text-xl">Click / Tap here!</p>
    </div>
</template>

<script>
export default {
    props: ['delay'],
    data() {
        return {
            showBlock: false,
            timer: null,
            reactionTime: 0,
        }

    },

    mounted() { //note 1/ lifecycle hooks
        setTimeout(() => { //executes the following code after this.delay
            this.showBlock = true
            // console.log("content shown after: " + this.delay) //just to show the different delays 
            //now that we have shown the content, lets call a method that counts the time
            this.startTimer() //note 2/ the timer


        }, this.delay);
    },

    methods: {
        startTimer() {
            this.timer = setInterval(() => {
                this.reactionTime += 10
            }, 10);
        },

        stopTimer() {
            clearInterval(this.timer)
            this.$emit('end', this.reactionTime) //note 3


        }
    },

    updated() {
        // console.log("component updated")
    },

    unmounted() {
        // console.log("component unmounted") //this will fire if we comment out the <block /> in app.vue. we will use this method for navigating between pages
    }


}

</script>


<style></style>

<!--
    note1/mounted():  there are some lifecycle hooks that we can use. mounted is one of them. when we click on the button in App.vue, it mounts block.vue before
    even showing its contents. so if we use mounted() method, we can dictate when to show the contents. 
    there are other hooks such us unmounted, beforeCreate, created.... 
    so we click on the button, it mounts block.vue, we run some code in mounted(), and using those codes we decide when to show the content. remember that we
    passed the delay as prop form the App.vue to here. remember we used mounted() independently not inside methods: because it is an inbuilt method.

    so when we click, console log shows: component mounted. then after an interval, console logs : contnet shown after.. and at the same time "component updated"



note2/ timer: so after we show the content, we also want to start a timer until the user clicks on the div. so in mounted() after we make showBlock to true,
we fire the startTimer method. in methods: we make that startTimer function. in the startTimer function, we make in setInterval function that fires every
10 seconds, and every 10 seconds we increment reactionTimer by 10 seconds. and the reason we store setInterval inside this.timer, is to store the value
of reactionTime and not increase it further. say the user clicked after 300 miliseconds, by using stopTimer we stop the interval from
firing and the last value of reactionTime will be 300miliseconds. 

note3/ now that we have captured the value of reactoinTime, in order to use it in the App.vue, we can emit it, just like we used to emit a word
that we use dn App.vue in order to evoke a method, here we can also pass an argument which is reactionTime. now go back to App.vue notes

-->