<template>
<h1 class="text-4xl mt-10 font-semibold max-sm:px-2 text-center mx-auto">Ninja Reaction Timer</h1>
<button @click="start" :disabled="isPlaying"  class="text-white py-1 px-3 rounded bg-cyan-600 0 my-6 cursor-pointer">play</button> <!--note 1/ :disabled-->

<Block v-if="isPlaying" :delay="delay" @end="endGame"/> <!--note 2--> <!--note 4/endGame-->
<Result v-if="showResults" :score="score"/>

</template>

<script>

import Block from "./components/Block.vue"
import Result from "./components/Result.vue"

export default {
  name: 'App',
  components: {
    Block,
    Result
   
  },
  data (){
    return {
      isPlaying: false,
      delay: null,
      score: null,
      showResults: false,
    }
  },
  methods: {
   start () { //note 3
    this.isPlaying = true
    this.delay = 2000 + Math.random() * 5000 //gives us a number between 2000 to 7000
    this.showResults = false
    console.log("component mounted") 
   },

   endGame(reactionTime){ //reactionTime here is the reactionTime we passed as an argument when we emitted it in stopTimer in block.vue. it does not need to be the same name.
    this.score = reactionTime
    this.isPlaying = false
    this.showResults = true

   }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #444;
  margin-top: 60px;
}
.transparent {
  background-color: rgba(0, 0green,0 blue,0.3 alpha);
}
button[disabled] {/*new thing */
  opacity: 0.2;
  cursor: not-allowed; 
}
</style>

<!--
note 1/ :disabled  : this is an inbuilt html code. it disabled the button while a condition is true.
note 2/ we pass the delay as a prop to the Block.vue, there we used to make the block appear after the delay.
note 3/ when we fire this function, it mounts the Block.vue in App.vue. and in Block.vue we make another method called mounted(), inside it, we make the block.vue content appear after the delay using setInterval method.
to understand methods such as mounted and others, which are called lifecycle hooks, rewatch the lesson at 12:49 https://youtu.be/bc6czIBLKTg?list=PL4cUxeGkcC9hYYGbV60Vq3IXYNfDk8At1
now go read the note 1/ for mounted() in the Block.vue


note4/ endgame: now we are back from Block.vue notes. last thing we did there was emit the word end, and the argument reactionTime. here
we can use the word 'end' to fire a last function called endGame. inside it we will set isPlaying back to false, so this will unmount
the block.vue, and will also enable the button again so we can play again, and the reaction time now can be dispalyed using the 'score' data.
but in order to use score in the result.vue, we make it a prop.
-->