<template>
<p class="text-gray-500 mt-10"> Reaction Time: {{ score }} ms</p>
<p class="text-green-600 text-3xl my-3 font-extrabold" ref="rank">{{ rank }}</p>

</template>

<script>
export default {
    props: ['score'], //remember not to write in built methods and props inside data() or methods:, respectively.
    data() {
        return {
            rank: null
        }
    },

    mounted(){
        if (this.score < 350) {
            this.rank = "Ninja Fingers!"
            this.$refs.rank.style.color = "orange"
        } else if (this.score < 500) {
            this.rank = "Rapid Reflexes.."

        }else {
            this.rank = "Snail Pace.."
            this.$refs.rank.style.color = "red"

        }
    }
}


</script>